.button {
    border: 2px solid #d2bdff;
    
    /* width: 200px;height: 100px; */
    border-radius: 4px;
    position: fixed;
    cursor: pointer;
    font-family: "Roboto", sans-serif;
    margin: 1px;
  }
  .button::after {
    content: "HOVER ME";
    position: absolute;
    top: 41%;
    left: 50%;
    -webkit-transform: translate(-50%);
            transform: translate(-50%);
    color: #d2bdff;
    transition: all 0.5s linear;
  }
  .button::before {
    content:normal;
    position: relative;
    display: block;
    height: 50px;
    width: 15px;
    background: #d2bdff;
    box-shadow: 0px 0px 10px 3px #0ff;
    margin-left: -18px;
    margin-top: -8px;
    opacity: 0;
    transition: all 0.5s linear;
    border-radius: 100%;
    -webkit-animation: star 5s linear infinite;
            animation: star 5s linear infinite;
  }
  @-webkit-keyframes star {
    0% {
      margin-left: -8px;
      margin-top: -8px;
    }
    25% {
      margin-left: 194px;
      margin-top: -8px;
    }
    50% {
      margin-left: 194px;
      margin-top: 75px;
    }
    75% {
      margin-left: -8px;
      margin-top: 75px;
    }
    100% {
      margin-left: -8px;
      margin-top: -8px;
    }
  }
  @keyframes star {
    0% {
      margin-left: -8px;
      margin-top: -8px;
    }
    25% {
      margin-left: 194px;
      margin-top: -8px;
    }
    50% {
      margin-left: 194px;
      margin-top: 75px;
    }
    75% {
      margin-left: -8px;
      margin-top: 75px;
    }
    100% {
      margin-left: -8px;
      margin-top: -8px;
    }
  }
  .button:hover::before {
    opacity: 1;
    -webkit-animation: star 5s linear infinite;
            animation: star 5s linear infinite;
  }
  .button:hover::after {
    content: "I'M GLOWING";
    text-shadow: 0 0 10px #fff, 0 0 10px #fff, 0 0 20px #82bedc, 0 0 30px #82bedc,
      0 0 40px #82bedc, 0 0 50px #82bedc, 0 0 60px #82bedc;
  }

  
  .button2  {
    position:absolute;
    display:flex;
    padding:25px 25px;
    align-items: center; 
    justify-content: space-between;
    border-radius: 25px;
    color: #000000;
    margin-left:260px;
    text-decoration: none;
    text-transform: uppercase;
    overflow: hidden;
    margin-top: -30px;
    font-weight: 600;
    font-size: large;
    font-family: 'Arial', sans-serif;
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
    border: 2px solid #d2bdff;
    transition: all 0.2s linear;
    cursor: pointer;
    height: 8px;
  }
  .button2:hover {
    border: 1px solid transparent;
    background-color: #020202;
    color: white;
  }
  .button2:hover span {
    position: absolute;
    display: block;
  }
  .button2:hover span:nth-child(1) {
    -webkit-filter: hue-rotate(0deg);
            filter: hue-rotate(0deg);
    top: 0;
    left: 0;
    width: 100%;
    height: 3px;
    background: linear-gradient(90deg, transparent, #3a86ff);
    -webkit-animation: animate1 1.5s linear infinite;
            animation: animate1 1.5s linear infinite;
  }
  @-webkit-keyframes animate1 {
    0% {
      left: -100%;
    }
    50%,
    100% {
      left: 100%;
    }
  }
  @keyframes animate1 {
    0% {
      left: -100%;
    }
    50%,
    100% {
      left: 100%;
    }
  }
  .button2:hover span:nth-child(2) {
    -webkit-filter: hue-rotate(60deg);
            filter: hue-rotate(60deg);
    top: -100%;
    right: 0;
    width: 3px;
    height: 100%;
    background: linear-gradient(180deg, transparent, #3a86ff);
    -webkit-animation: animate2 1.5s linear infinite;
            animation: animate2 1.5s linear infinite;
    -webkit-animation-delay: 0.50s;
            animation-delay: 0.50s;
  }
  @-webkit-keyframes animate2 {
    0% {
      top: -100%;
    }
    50%,
    100% {
      top: 100%;
    }
  }
  @keyframes animate2 {
    0% {
      top: -100%;
    }
    50%,
    100% {
      top: 100%;
    }
  }
  .button2:hover span:nth-child(3) {
    -webkit-filter: hue-rotate(120deg);
            filter: hue-rotate(120deg);
    bottom: 0;
    right: 0;
    width: 100%;
    background: linear-gradient(270deg, transparent, #3a86ff);
    -webkit-animation: animate3 1.5s linear infinite;
            animation: animate3 1.5s linear infinite;
    -webkit-animation-delay: 0.9s;
            animation-delay: 0.9s;
  }
  @-webkit-keyframes animate3 {
    0% {
      right: -100%;
      height: 3px;
    }
    50%,
    100% {
      height: 2px;
      right: 100%;
    }
  }
  @keyframes animate3 {
    0% {
      right: -100%;
      height: 3px;
    }
    50%,
    100% {
      height: 2px;
      right: 100%;
    }
  }
  .button2:hover span:nth-child(4) {
    -webkit-filter: hue-rotate(300deg);
            filter: hue-rotate(300deg);
    bottom: -100%;
    left: 0;
    width: 3px;
    height: 100%;
    background: linear-gradient(360deg, transparent, #0062ff);
    -webkit-animation: animate4 1.5s linear infinite;
            animation: animate4 1.5s linear infinite;
    -webkit-animation-delay: 1.2s;
            animation-delay: 1.2s;
  }
  @-webkit-keyframes animate4 {
    0% {
      bottom: -100%;
    }
    50%,
    100% {
      bottom: 100%;
    }
  }
  @keyframes animate4 {
    0% {
      bottom: -100%;
    }
    50%,
    100% {
      bottom: 100%;
    }
  }

